.page-header {
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: @color-font-main;

  background-color: @color-main;

  &--inner {
    margin-bottom: 27px;
  }

  @media (min-width: @tablet-width) {
    padding-top: 24px;

    margin-bottom: 64px;
  }

  @media (min-width: @desktop-width) {
    position: relative;

    margin-bottom: 74px;
    padding-top: 54px;

    background: none;
  }
}

.page-header-container {
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.index .page-header {
  @media (min-width: @desktop-width) {
    &::before {
      .absolute();
      top: 0;
      left: 50%;
      z-index: -1;

      width: 50%;
      height: 696px;

      background-color: #64a83b;
      background-image: url("../img/index-background-desktop@1x.jpg");
      background-size: 720px auto;
      background-repeat: no-repeat;
      background-position: right;
    }

    @media (min-resolution: 2dppx) {
      &::before {
        .absolute();
        top: 0;
        left: 50%;
        z-index: -1;

        width: 50%;
        height: 696px;

        background-image: url("../img/index-background-desktop@2x.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: right;
      }
    }
  }
}

.page-header__wrapper {
  position: relative;

  &::before {
    .absolute();
    left: 50%;
    bottom: 0;
    width: 100vw;
    height: 1px;
    background-color: #e6e6e6;
  }

  &::after {
    .absolute();
    right: 50%;
    bottom: 0;
    width: 100vw;
    height: 1px;
    background-color: #e6e6e6;
  }

  @media (min-width: @tablet-width) {
    width: 192px;

    &::before,
    &::after {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    width: 610px;

    border: none;
  }
}

.page-header__logo {
  display: flex;
  align-items: center;
  width: 100%;
  padding-top: 14px;
  padding-bottom: 10px;

  &[href]:hover {
    opacity: 0.8;
  }

  &[href]:active {
    opacity: 0.6;
  }

  @media (min-width: @tablet-width) {
    width: 192px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    width: 610px;
    padding: 0;
  }
}

.page-header__logo-img {
  @media (min-width: @tablet-width) {
    width: 173px;
    height: 58px;
  }

  @media (min-width: @desktop-width) {
    width: 202px;
    height: 59px;
  }
}
